<template>
  <div>
    <div id="dkcx1">
      <!-- 顶部导航 -->
      <div class="nav">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>客户管理</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- <div class="searchGroup"></div> -->
      </div>

     

      <!-- 右方操作 -->
      <div class="table_container" style="overflow-y: auto;">
           <div class="flowSearchBar" >
            
            <div class="normalBar">
              <el-row :gutter="10">
                <el-col :span="8">
                  <div class="grid-content bg-purple">
                    <el-input v-model="search_gsmc" placeholder="公司名称搜索"></el-input>
                  </div>
                </el-col>
                
                <el-col :span="4">
                  <el-select v-model="search_cp" placeholder="产品">
                    <el-option label="全部" value="00">全部</el-option>
                    <el-option
                      v-for="item in prodList"
                      :key="item.prodUuuid"
                      :label="item.prodName"
                      :value="item.prodUuid">
                    </el-option>
                  </el-select>
                </el-col>
                <el-col :span="4">
                  <el-select v-model="search_sqzt" placeholder="申请状态">
                    <el-option label="全部" value="00">全部</el-option>
                    <el-option
                      v-for="item in sqztList"
                      :key="item.dm"
                      :label="item.dmmc"
                      :value="item.dm">
                    </el-option>
                  </el-select>
                </el-col> 
                <el-col :span="4">
                  <el-select v-model="search_lx" placeholder="类型">
                    <el-option label="全部" value="00">全部</el-option>
                    <el-option
                      v-for="item in typeList"
                      :key="item.dm"
                      :label="item.dmmc"
                      :value="item.dm">
                    </el-option>
                  </el-select>
                </el-col>
                <el-col :span="4">
                  <div class="grid-content bg-purple search-button">
                    <el-button type="primary" icon="el-icon-search" @click="searchFun">&nbsp;搜索&nbsp;</el-button>
                  </div>
                </el-col>
                
              </el-row>
            </div>
            <div class="normalBar">
              <el-row :gutter="10">
                <el-col :span="8">
                  <div class="grid-content bg-purple">
                    <el-input v-model="search_qdmc" placeholder="渠道商名称搜索"></el-input>
                  </div>
                </el-col> 
                <el-col :span="4">
                  <div class="grid-content bg-purple">
                    <el-input v-model="search_frlxdh" placeholder="法人联系电话搜索"></el-input>
                  </div>
                </el-col>  
                <el-col :span="4">
                  <div class="grid-content bg-purple">
                    <el-input v-model="search_frxm" placeholder="法人姓名搜索"></el-input>
                  </div>
                </el-col>       
                <el-col :span="8">
                  <el-date-picker
                    size="medium"
                    v-model="search_sqrq"
                    type="daterange"
                    align="right"
                    unlink-panels
                    range-separator="至"
                    start-placeholder="申请日期起"
                    end-placeholder="申请日期止"
                    format="yyyy 年 MM 月 dd 日"
                    value-format="yyyy-MM-dd"
                    :picker-options="pickerOptions"
                  ></el-date-picker>
                </el-col>
              </el-row>
            </div>
          </div>
          <!-- <div style="overflow-y: auto;"> -->
            <el-table v-loading="loading" :data="dataList" border style="width: 100%;">
              <!-- <el-table-column type="expand">
                <template slot-scope="props">
                  <div class="morePanel">
                    <div class="morepalel_header">
                      <div class="mutiButton" @click="followUp(props.row)">
                        <i class="el-icon-edit-outline"></i>
                        写跟进
                      </div>
                    </div>

                    
                    <div class="line"></div>
                    <input :checked="!tableRows[props.$index].isSideBoxExpand" class="row_sideBox" @change="tableRows[props.$index].isSideBoxExpand=!tableRows[props.$index].isSideBoxExpand" style="display:none;" type="checkBox" name="row_sideBox_check" :id="'row_sideBox_check'+props.$index">
                    <div class="sideBox" >
                      <div class="tracehistory">
                        <div class="singlehistory" v-for="item in props.row.logList" :key="item.SQ_UUID">
                          <div class="traceInfo">
                            <img src="../../assets/imgs/userPartner.png" alt class="userIcon">
                            <span class="tracername">{{item.RY_XM}}({{item.RY_LXDH}})</span>于
                            <span class="time">{{item.LRSJ}}</span>
                          </div>
                          <div class="traceContent">
                            <span>贷款状态：</span>
                            {{item.SQZTMC}} &nbsp;&nbsp;&nbsp;
                            <span>跟进状态：</span>
                            {{item.GJZT}}
                          </div>
                          <div class="traceContent">
                            <span>跟进内容：</span>
                            {{item.LOG}}
                          </div>
                        </div>
                        <div class="emptyBox" v-if="typeof(props.row.logList)=='undefined'">暂无跟进信息</div>
                      </div>
                      <div class="otherInfo">
                        <h5>其他信息</h5>
                        <div class="otherInfoBox">
                          <span class="infoTitle">法人身份证号码:</span>
                          <span>{{ props.row.FR_SFZJHM }}</span>
                        </div>
                        <div class="otherInfoBox">
                          <span class="infoTitle">纳税人识别号:</span>
                          <span>{{ props.row.NSRSBH }}</span>
                        </div>
                        <div class="line"></div>
                      </div>
                    </div>
                  </div>
                </template>
              </el-table-column> -->
              <el-table-column label="产品名称" prop="prodName" width="110"></el-table-column>
              <el-table-column label="公司名称" prop="gsmc" ></el-table-column>
              <!-- <el-table-column label="纳税人识别号" prop="NSRSBH" ></el-table-column> -->
              <el-table-column label="法人姓名" prop="frXm" width="100"></el-table-column>
              <el-table-column label="法人手机号" prop="frPhone" width="120"></el-table-column>
              <!-- <el-table-column label="法人身份证号码" prop="FR_SFZJHM" width="170"></el-table-column> -->
              <el-table-column label="渠道商名称" prop="qdsMc" width="100"></el-table-column>
              <el-table-column label="申请状态" prop="sqztmc" width="80"></el-table-column>
              <el-table-column label="放款金额（万元）" prop="sjfkje"  width="140"></el-table-column>
              <el-table-column label="申请日期" prop="sqrq" width="160"></el-table-column>
              <el-table-column label="操作" width="100" align="center">
                <template slot-scope="props">
                  <el-button type="success" size="mini" style="margin:0 auto;" @click.native="followUp(props.row)" round>写跟进</el-button>
                </template>
              </el-table-column>
            </el-table>

            <div class="table_info_bar" style="position: fixed;bottom: 40px;padding-right: 227px;box-sizing: border-box;">
              <!-- <div class="someInfoBox">
                <span class="single">总金额：{{zje}}万元</span>
              </div> -->
              <div class="block">
                <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page.sync="currentPage"
                  :page-sizes="[100, 200, 300, 400]"
                  :page-size="10"
                  layout="total, prev, pager, next, jumper"
                  :total="totalSize"
                ></el-pagination>
              </div>                                                                                            
            </div>
      </div>

      <!-- 跟进弹出层 -->
      <el-dialog title="跟进" :visible.sync="dialogTableVisibleGj" id="bumpForm" width="70%" :close-on-click-modal="false">
        <el-form :model="editData" v-model="editData.sqUuid">
          <fieldset class="fieldset">
            <legend class="legend">贷款信息</legend>
            <el-row>
              <el-col :span="24">
                <div class="grid-content">
                  <el-form-item label="企业名称" :label-width="formLabelWidth">
                    <el-input v-model="editData.qymc" auto-complete="off" :disabled="!isValiteCode2"></el-input>
                  </el-form-item>
                </div>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="12">
                <div class="grid-content">
                  <el-form-item label="产品名称" :label-width="formLabelWidth">
                    <el-input v-model="editData.dkcp" auto-complete="off" :disabled="!isValiteCode2"></el-input>
                  </el-form-item>
                </div>
              </el-col>
              <el-col :span="12">
                <div class="grid-content">
                  <el-form-item label="申请状态" :label-width="formLabelWidth">
                    <el-input v-model="editData.sqztmc" auto-complete="off" :disabled="!isValiteCode2"></el-input>
                  </el-form-item>
                </div>
              </el-col>
            </el-row>
            <el-row>              
              <el-col :span="12">
                <div class="grid-content">
                  <el-form-item label="申请日期" :label-width="formLabelWidth">
                    <el-input v-model="editData.sqrq" auto-complete="off" :disabled="!isValiteCode2"></el-input>
                  </el-form-item>
                </div>
              </el-col>
            </el-row>
          </fieldset>
          <el-row>
            <el-col :span="12">
              <div class="grid-content">
                <el-form-item label="申请状态" :label-width="formLabelWidth">
                  <el-select v-model="editData.sqzt" placeholder="申请状态"  @change="sqztChange" :disabled="isValiteCode6">
                    <el-option v-for="item in sqztList" :key="item.dm" :label="item.dmmc" :value="item.dm"></el-option>
                  </el-select>
                </el-form-item>
              </div>
            </el-col>   
            <el-col :span="12" v-if="isValiteCode">
              <div class="grid-content">
                <el-form-item label="放款金额" :label-width="formLabelWidth">
                  <el-input v-model="editData.sjfkje" auto-complete="off" placeholder="请输入实际放款金额(万元)"></el-input>
                </el-form-item>
              </div>
            </el-col>        
          </el-row>
         
          <el-row>
            <el-col :span="24">
              <div class="grid-content">
                <el-form-item label="备注" :label-width="formLabelWidth">
                  <el-input v-model="editData.bz" auto-complete="off" type="textarea" placeholder="请输入备注"></el-input>
                </el-form-item>
              </div>
            </el-col>
          </el-row>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="cancelThis">取 消</el-button>
          <el-button type="primary" @click="saveGj">确 定</el-button>
        </div>
      </el-dialog>

    </div>
  </div>
</template>

<style lang="scss" scope>
@import './clientManage.scss';
</style>

<script>
import Axios from 'axios';
var menu_uuid = 'E04215923DC8406FAC43F9C05D60138C';
var defalutRows=[
   {isLogExpand:false,isSideBoxExpand:true},
  {isLogExpand:false,isSideBoxExpand:true},
  {isLogExpand:false,isSideBoxExpand:true},
  {isLogExpand:false,isSideBoxExpand:true},
  {isLogExpand:false,isSideBoxExpand:true},

  {isLogExpand:false,isSideBoxExpand:true},
  {isLogExpand:false,isSideBoxExpand:true},
  {isLogExpand:false,isSideBoxExpand:true},
  {isLogExpand:false,isSideBoxExpand:true},
  {isLogExpand:false,isSideBoxExpand:true},

  {isLogExpand:false,isSideBoxExpand:true},
  {isLogExpand:false,isSideBoxExpand:true},
  {isLogExpand:false,isSideBoxExpand:true},
  {isLogExpand:false,isSideBoxExpand:true},
  {isLogExpand:false,isSideBoxExpand:true},

  {isLogExpand:false,isSideBoxExpand:true},
  {isLogExpand:false,isSideBoxExpand:true},
  {isLogExpand:false,isSideBoxExpand:true},
  {isLogExpand:false,isSideBoxExpand:true},
  {isLogExpand:false,isSideBoxExpand:true},
  ]
export default {
  data() {
    return {
      tableRows:defalutRows,
      isValiteCode6:false,
      isValiteCode:false,
      isValiteCode2:false,
      fullscreenLoading:false,
      search_gsmc: '',
      search_sqrq:'',
      search_sqzt:'',
      search_lx:'',
      search_cp:'',
      search_qdmc:'',
      search_frlxdh:'',
      search_frxm:'',
      typeList:[],
      currentPage: 1,
      totalSize: 0,
      dataList: [],
      sqztList:[],
      loading: false,
      dialogTableVisibleGj: false,
      editData: {
        sqzt: '',
        sqztmc:'',
        bz: '',
        sqUuid:''
      },
      userInfo: {},
      isValiteCode2: false,      
      formLabelWidth: '78px',
      prodList:[],
      pickerOptions: {
        shortcuts: [
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }
        ]
      },
    };
  },
  methods: {     
    handleCurrentChange(val) {
      this.queryOrder(this.search_gsmc,val);
    },
    // 点击搜索
    searchFun() {
      this.currentPage = 1;
      this.queryOrder(this.search_gsmc,1);
    },
    // 表格数据刷新函数
    queryOrder(_search_gsmc,_start) {
      let qs = '';
      let qz = '';
      if (this.search_sqrq) {
        qs = this.search_sqrq[0];
        qz = this.search_sqrq[1];
      }
      var _lx=this.search_lx;
      if(this.search_lx=="00"){
        _lx="";
      }

      var _sqzt=this.search_sqzt;
      if(this.search_sqzt=="00"){
        _sqzt="";
      }
      var _cp=this.search_cp;
      if(this.search_cp=="00"){
        _cp="";
      }

      var that = this;
      var json={
        "gsmc":_search_gsmc || '',
        "prodUuid":_cp || '',
        "qdsMc":this.search_qdmc || '',
        "sqzt":_sqzt || '',
        "lx":_lx || '',
        "frPhone":this.search_frlxdh || '',
        "frXm":this.search_frxm || '',
        "sqrqq":qs || '',
        "sqrqz":qz || ''
      }
       $.ajax({
          type: 'POST',
          async: true,
          url:this.projectName + 'order/queryOrders?pageNum='+_start,
          data:JSON.stringify(json),
          headers:{
              'Authorization':sessionStorage.getItem("tokenHead")+sessionStorage.getItem("token")
          },
          contentType:"application/json",
          dataType: "json",
          timeout: 60000,
          success: function(data, textStatus, jqXHR) {
            console.log(data);
            if (data.code == 200) {            
              that.dataList = data.data.list;
              that.totalSize = data.data.total;
            } else {
              that.dataList = [];
              that.totalSize = 0;
            }          
            that.loading = false;
         },
        error: function(xhr, textStatus) {
          console.log(xhr);
        }
        });
    },
    //点击写跟进
    followUp(row) {
      console.log(row);
      this.editData.sqztmc = row.sqztmc;
      this.editData.sqzt = row.sqzt;
      this.editData.qymc = row.gsmc;
      this.editData.dkcp = row.prodName;
      this.editData.sqrq = row.sqrq;
      this.editData.sqUuid=row.sqUuid;
      this.editData.bz=row.bz;
      if (this.editData.sqzt == '2') {
        this.editData.sjfkje =row.sjfkje;
        this.isValiteCode=true;
        this.isValiteCode6=true;
      }else{
        this.isValiteCode6=false;
        this.isValiteCode=false;
      }
      this.dialogTableVisibleGj = true;
      
    }, 
    //跟进贷款申请状态下拉选择值判断是否要显示放款金额一列
    sqztChange() {
      if (this.editData.sqzt == '2') {
        this.isValiteCode = true;
      } else {
        this.isValiteCode = false;
      }
    },
    //保存跟进信息
    saveGj() {
      if (this.editData.sqzt == '' || this.editData.sqzt == null) {
        this.$message({
          type: 'info',
          message: '请选择申请状态'
        });
        return false;
      }

      if (this.editData.sqzt == '2') {
        if (this.editData.sjfkje == '' || this.editData.sjfkje == null) {
          this.$message({
            type: 'info',
            message: '请输入实际放款金额（万元）'
          });
          return false;
        }
      }

      if (this.editData.bz == '' || this.editData.bz == null) {
        this.$message({
          type: 'info',
          message: '请输入备注信息'
        });
        return false;
      }
      var that = this;
      var json={
          "sqzt": this.editData.sqzt,
          "bz": this.editData.bz,
          "sqUuid": this.editData.sqUuid,
          "sjfkje":this.editData.sjfkje
      }
      $.ajax({
        type: 'POST',
        async: true,
        url: this.projectName + 'order/updateOrder',
        data:JSON.stringify(json),
        headers:{
            'Authorization':sessionStorage.getItem("tokenHead")+sessionStorage.getItem("token")
        },
        contentType:"application/json",
        dataType: "json",
        timeout: 60000,
        success: function(data, textStatus, jqXHR) {
          if (data.code == 200) {
            that.$message({
              type: 'info',
              message: data.message
            });
            that.dialogTableVisibleGj = false;
            that.queryOrder(that.search_gsmc,1);
          } else {
            that.$message({
              type: 'error',
              message: data.message
            });
          }
        },
        error: function(xhr, textStatus) {
          console.log(xhr);
        }
        });
    }, 
    // 跟进弹出层取消
    cancelThis() {
      this.dialogTableVisibleGj = false;
    }, 
    // 初始化查询类型
    initDate() {
      var that = this;
       Axios({
          method:'post',
          url:this.projectName+'common/getCodeTable',
          params:{
            dmType:'贷款申请状态'
          },
          headers:{
              'Authorization':sessionStorage.getItem("tokenHead")+sessionStorage.getItem("token")
          },
          contentType:"application/json",
          dataType: "json",
          }).then(function (response) {
            console.log("贷款申请状态:",response.data);
            if (response.data.code == 200) {
              that.sqztList = response.data.data;     
            }
        })
        .catch(function (error) {
          
        })

        Axios({
          method:'post',
          url:this.projectName+'common/getCodeTable',
          params:{
            dmType:'贷款申请类型'
          },
          headers:{
              'Authorization':sessionStorage.getItem("tokenHead")+sessionStorage.getItem("token")
          },
          contentType:"application/json",
          dataType: "json",
          }).then(function (response) {
            console.log("贷款申请类型:",response.data);
            if (response.data.code == 200) {
              that.typeList = response.data.data;     
            }
        })
        .catch(function (error) {
          
        })

       
         var json={};
         $.ajax({
            type: 'POST',
            async: true,
            url:this.projectName+'prod/queryProd',
            data:JSON.stringify(json),
            headers:{
                'Authorization':sessionStorage.getItem("tokenHead")+sessionStorage.getItem("token")
            },
            contentType:"application/json",
            dataType: "json",
            timeout: 60000,
            success: function(data, textStatus, jqXHR) {
               if (data.code == 200) {
                // console.log("所有产品：",data);
                that.prodList = data.data;     
              } else {
                that.$message({
                  type: 'warning',
                  message: data.message
                });
              }
          },
          error: function(xhr, textStatus) {
            console.log(xhr);
          }
          });
      
    }
    
  },
  created: function() {
   
  },
  mounted: function() {
    var userInfo = {};
    if (userInfo) {
      userInfo = JSON.parse(sessionStorage.getItem('userInfo'));
    } else {
      userInfo = {};
    }
    this.initDate();
    this.queryOrder(this.search_gsmc,1);
    this.userInfo = userInfo;

  }
};
</script>


